body {
    margin: 0;
    font-family: 'Bebas Neue', sans-serif;
    font-size: 18px;
    color: #fff;
    -webkit-font-smoothing: antialiased;
    background-color: #fff;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
  }
  p {
    margin: 0 0 10;
  }
  *,
  *:before,
  *:after {
    box-sizing: border-box;
  }
  
.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
  }
  a{
      text-decoration: none;
      color: #fff;
  }
  .bigtitle{
    font-weight: 400;
    font-size: 80px;
    line-height: 80px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #FFFFFF;
  }
  .btn--yellow{
    background: linear-gradient(182.88deg, #FFB548 11.27%, #F3AF34 90.4%);
    border-radius: 4px;
    padding:20px 22px ;
    border: none;
    cursor:pointer;
    outline:none;
    &:hover{
        background: rgb(255, 183, 49);
    }
    
  }

  .header{
    position: absolute;
    width: 100%;
    &__inner{
        padding: 20px;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    &__logo{
        width: 55px;
        height: 55px;
    }
    .nav{
        display: flex;
        &__menu a{
            color: #fff;
            text-transform: uppercase;
            transition: color .2s linear;
            +a{
                margin-left: 38px;
            }
            &:hover{
                color: #FFB548;
            }
        }
        &__select{
            margin-left: 150px;
            &-header{
                display: flex;
                position: relative;
                cursor:pointer;
                color: #fff;
                transition: color .2s linear;
                &:hover{
                    color: #FFB548;
                }

            }
            &-body .is-active {
                display: block;
            }
            &-icon{
                position: absolute;
                padding-left: 25px;
                bottom: 4px;
                img{
                }
            }
            &-current{
            }
            &-item{
                color: #fff;
            }
            &-body{
                display: none;
                background-color: black;
                border-radius: 4px;
                padding:5px 20px 5px 5px;
                color: #fff;
                transition: color .2s linear;
                cursor: pointer;
                &:hover{
                    color: #FFB548;
                }
                
            }
            &-item a{
                transition: color .2s linear;
                &:hover{
                    color: #FFB548;
                }
            }
        }
    }
    &__icons{
        margin-left: 13px;
        &-xbox{
            cursor: pointer;
            width: 30px;
            height: 27px;
        }
        &-steam{
            margin-left: 13px;
            width: 30px;
            height: 27px;
            cursor: pointer;
        }
    }
}


.intro{
    padding: 120px 0 192px 0;
    background: url(../img/intro-background.png);
    background-size:cover;
    background-repeat: no-repeat;
    background-position: center;
    &__inner{
        margin: 143px;
        text-align: center;
    }
    &__title{
        
    }
    &__subtitle{
        font-weight: 400;
        font-size: 24px;
        line-height: 24px;
        letter-spacing: 2px;

        color: #FFFFFF;
        margin-bottom: 22px;

    }
    &__button{
        font-family: Open Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 22px;
        color: #1A1917;
    }
    &__story{
        margin-top: 250px;
            display: flex;
            flex-direction:row;
            justify-content: center;
        &-text{
            position: relative;
        }
        &-icon{
            position: absolute;
            bottom: -83px;
        }
        
    }
}
